const Mixins = {
  data() {
    return {
      maxDate: this.addDate(new Date(), 90),
      // :90
      workingDay: 0,
      holiday: 0,
      booking: {
        start: '',
        end: '',
        date: [],
      },
    };
  },
  computed: {
    totalprice() {
      return this.singleRoom
        ? this.singleRoom.holidayPrice * this.holiday + this.singleRoom.normalDayPrice
        * this.workingDay + this.serviceFee : 0;
    },
  },
  methods: {
    addDate(date, days) {
      const daylen = days || 0;
      const newDate = new Date(Number(date));// 
      newDate.setDate(date.getDate() + daylen);// 
      return newDate;
    },
    resetDate() {
      this.booking.start = '';
      this.booking.end = '';
    },
    updateDate(date) {
      this.booking.start = new Date(date.start);
      // calenderconfirmdate:{start:..., end:...}
      this.booking.end = new Date(date.end);// bookingstart/end
      this.countDate();// date[]
    },
    countDate(start, end) {
      this.booking.date = [];
      let workingDay = 0;
      let holiday = 0;
      const from = start ? new Date(start) : this.booking.start;
      const to = end ? new Date(end) : this.booking.end;
      while (from < to) { // 
        const day = from.getDay();
        if ((day === 0) || (day === 6)) {
          holiday += 1;
        } else {
          workingDay += 1;
        }
        if (!start && !end) {
          this.booking.date.push(this.dateFormat(from)); // api
        }
        from.setDate(from.getDate() + 1);// 1
      }
      this.workingDay = workingDay;
      this.holiday = holiday;
    },
    dateFormat(date) {
      const year = date.getFullYear().toString();// number
      let month = (date.getMonth() + 1).toString();
      let day = date.getDate().toString();
      month = month.length === 1 ? `0${month}` : month;
      day = day.length === 1 ? `0${day}` : day;
      return `${year}-${month}-${day}`;
    },
  },
};

export default Mixins;
